"use client";

import { useEffect, useState } from "react";
import ConfirmModal from "./ConfirmModal";

type ActivityRow = {
  activityId: string;
  activityName: string;
  budget: number;
};

type RawBudgetRow = {
  engagement: string;
  activityId: string;
  activity: string;
  budget: number;
};

type Props = {
  engagement: string;
};

export default function BudgetEditor({ engagement }: Props) {
  const [activities, setActivities] = useState<ActivityRow[]>([]);
  const [editedBudgets, setEditedBudgets] = useState<Record<string, string>>({});
  const [selectedActivity, setSelectedActivity] = useState<string | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);

  useEffect(() => {
    console.log("üß™ isModalOpen „ÅÆÁä∂ÊÖã:", isModalOpen);
  }, [isModalOpen]);

  // 1. „Éá„Éº„ÇøÂèñÂæó
  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await fetch("/api/fetchBudgetAndActuals");
        const allData: RawBudgetRow[] = await res.json();

        const filtered = allData
          .filter((d) => d.engagement === engagement)
          .reduce((map: Record<string, ActivityRow>, row) => {
            if (!map[row.activityId]) {
              map[row.activityId] = {
                activityId: row.activityId,
                activityName: row.activity,
                budget: row.budget,
              };
            }
            return map;
          }, {});

        const activityList = Object.values(filtered).sort((a, b) =>
          a.activityId.localeCompare(b.activityId)
        );
        setActivities(activityList);
      } catch (err) {
        console.error("‚ùå „Éá„Éº„ÇøÂèñÂæó„Ç®„É©„Éº:", err);
      }
    };

    fetchData();
  }, [engagement]);

  // 2. ÂÖ•ÂäõÂÄ§Êõ¥Êñ∞
  const handleChange = (activityId: string, value: string) => {
    setEditedBudgets({ ...editedBudgets, [activityId]: value });
  };

  // 3. „É¢„Éº„ÉÄ„É´Ë°®Á§∫
  const handleUpdate = (activityId: string) => {
    console.log("üü° Êõ¥Êñ∞„Éú„Çø„É≥Êäº‰∏ã - activityId:", activityId);
    setSelectedActivity(activityId);
    setIsModalOpen(true);
  };

  // 4. ÂÆüÈöõ„ÅÆÊõ¥Êñ∞Âá¶ÁêÜ
  const confirmUpdate = async () => {
    if (!selectedActivity) return;

    const newBudget = editedBudgets[selectedActivity];
    if (newBudget === undefined || isNaN(Number(newBudget))) {
      alert("Êï∞ÂÄ§„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ");
      return;
    }

    try {
      const res = await fetch("/api/updateBudget", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          engagement,
          activityId: selectedActivity,
          newBudget: Number(newBudget),
        }),
      });

      if (!res.ok) throw new Error("Êõ¥Êñ∞„Å´Â§±Êïó„Åó„Åæ„Åó„Åü");

      const updatedList = activities.map((act) =>
        act.activityId === selectedActivity
          ? { ...act, budget: Number(newBudget) }
          : act
      );
      setActivities(updatedList);
      setIsModalOpen(false);
      alert("‚úÖ Êõ¥Êñ∞„Åó„Åæ„Åó„Åü");
    } catch (error) {
      console.error("‚ùå Êõ¥Êñ∞Â§±Êïó:", error);
      alert("Êõ¥Êñ∞„Å´Â§±Êïó„Åó„Åæ„Åó„Åü");
    }
  };

  return (
    <div className="border p-4 rounded shadow bg-white">
      <h3 className="text-lg font-semibold mb-4">Ê¥ªÂãïÂà•‰∫àÁÆóÁ∑®ÈõÜÔºö{engagement}</h3>

      <table className="w-full border-collapse">
        <thead>
          <tr className="bg-gray-100">
            <th className="border p-2">Activity ID</th>
            <th className="border p-2">ActivityÂêç</th>
            <th className="border p-2">‰∫àÁÆóÔºàhÔºâ</th>
            <th className="border p-2">Êìç‰Ωú</th>
          </tr>
        </thead>
        <tbody>
          {activities.map((act) => (
            <tr key={act.activityId} style={{ lineHeight: "1", height: "28px" }}>
              <td style={{ border: "1px solid #ccc", padding: "2px 4px" }}>
                {act.activityId}
              </td>
              <td style={{ border: "1px solid #ccc", padding: "2px 4px" }}>
                {act.activityName}
              </td>
              <td style={{ border: "1px solid #ccc", padding: "2px 4px" }}>
                <input
                  type="number"
                  value={editedBudgets[act.activityId] ?? act.budget.toString()}
                  onChange={(e) => handleChange(act.activityId, e.target.value)}
                  style={{
                    padding: "1px",
                    height: "20px",
                    fontSize: "12px",
                    width: "100px",
                  }}
                />
              </td>
              <td style={{ border: "1px solid #ccc", padding: "2px 4px" }}>
                <button
                  onClick={() => handleUpdate(act.activityId)}
                  style={{
                    backgroundColor: "#3b82f6",
                    color: "white",
                    padding: "2px 8px",
                    fontSize: "12px",
                    borderRadius: "4px",
                    height: "24px",
                  }}
                >
                  Êõ¥Êñ∞
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      <ConfirmModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onConfirm={confirmUpdate}
        message="‰∫àÁÆó„ÇíÊõ¥Êñ∞„Åó„Å¶„Çà„Çç„Åó„ÅÑ„Åß„Åô„ÅãÔºü"
      />
    </div>
  );
}
